window.dom = {
  //ÂàõÂª∫Êñ∞ÁöÑËäÇÁÇπ
  create(string) {
    const container = document.createElement("template");
    container.innerHTML = string.trim();
    console.log(container)
    return container.content.firstChild;
  },
  //ÂàõÂª∫Êñ∞ÁöÑÂºüÂºüËäÇÁÇπ
  after(node, node2) {
    // ‰ªéÁà∂ËäÇÁÇπÊâæÂà∞‰∏ã‰∏Ä‰∏™ÂÖÑÂºüËäÇÁÇπÔºåÊèíÂà∞‰∏ã‰∏Ä‰∏™ÂÖÑÂºüËäÇÁÇπÁöÑÂâçÈù¢ÔºõÂ¶ÇÊûúÊ≠§ËäÇÁÇπÊòØÊúÄÂêé‰∏Ä‰∏™ËäÇÁÇπÔºåÊó†Ê≥ïÊèíÂÖ•
    return node.parentNode.insertNode(node2, node.nextSibling);
  },
  //ÂàõÂª∫Êñ∞ÁöÑÂì•Âì•ËäÇÁÇπ
  before(node, node2) {
    return node.parentNode.insertNode(node2, node);
  },
  //ÂàõÂª∫Êñ∞ÁöÑÂÑøÂ≠êËäÇÁÇπ
  append(parent, node) {
    parent.appendChild(node)
  },
  //ÂàõÂª∫Êñ∞ÁöÑÁà∂ËäÇÁÇπ
  wrap(node, parent) {
    //ÂàõÂª∫Áà∂ËäÇÁÇπ‰Ωú‰∏∫ÂÖÑÂºüËäÇÁÇπ
    dom.before(node, parent)
    //ÊääÂΩìÂâçËäÇÁÇπÈôçÁ∫ß‰∏∫Â≠êËäÇÁÇπ
    dom.append(parent, node)
  },
  //Âà†Èô§‰∏Ä‰∏™Â≠êËäÇÁÇπ
  remove(node) {
    //Ê≠§Êé•Âè£ÊØîËæÉÊñ∞ÔºåIEÂèØËÉΩ‰∏çÊîØÊåÅ
    // node.remove()
    node.parentNode.removeChild(node)
    //‰øùÁïôËäÇÁÇπÁöÑÂºïÁî®
    return node
  },
  //Âà†Èô§ÊâÄÊúâÂ≠êËäÇÁÇπ
  empty(node) {
    //Âà†Èô§ÂêéÊó†Ê≥ïÂÜçËé∑ÂèñÂºïÁî®
    // node.innerHTML = ''
    //Êñ∞ËØ≠Ê≥ïÁ≠â‰ª∑‰∫éÔºöconst childNodes = node.childNodes
    const {
      childNodes
    } = node
    const array = []
    //‰ΩøÁî®Ê≠§ÁßçÈÅçÂéÜÊñπÂºèÔºåÊØèÊ¨°ÁöÑÈïøÂ∫¶ÈÉΩÊòØÂèòÂåñÁöÑ
    // for (let i = 0; i < childNodes.length; i++) {
    //   dom.remove(childNodes[i])
    //   array.push(childNodes[i])
    // }
    //ËøîÂõûÂºïÁî®
    let x = node.firstChild
    while (x) {
      array.push(dom.remove(node.childNodes))
      x = node.firstChild
    }
    return array
  },
  attr(node, name, value) { //ÈáçËΩΩ
    if (arguments.length === 3) {
      node.setAttribute(name, value)
    } else if (arguments.length === 2) {
      return node.getAttribute(name)
    }
  },
  text(node, string) { //ÈÄÇÈÖç
    if (arguments === 2) {
      if ('innerText' in node) {
        node.innerText = string //IE
      } else {
        node.textContent = string //Chrome
      }
    } else if (arguments === 1) {
      if ('innerText' in node) {
        return node.innerText
      } else {
        return node.textContent
      }
    }
  },
  html(node, string) {
    if (arguments === 2) {
      node.innerHTML = string
    } else if (arguments === 1) {
      return node.innerHTML
    }
  },
  style(node, name, value) {
    if (arguments === 3) {
      //dom.style(div,'color','red')
      node.style[name] = value
    } else if (arguments === 2) {
      if (typeof name === "stirng") {
        return node.style[name]
      } else if (name instanceof Object) {
        const object = name
        for (let key in object) {
          node.style[key] = object[key]
        }
      }
    }
  },
  class: {
    add(node, className) {
      node.classList.add(className)
    },
    remove(node, className) {
      node.classList.remove(className)
    },
    has(node, className) {
      node.classList.contains(className)
    }
  },
  on(node, eventName, fn) {
    node.addEventListener(eventName, fn)
  },
  off(node, eventName, fn) {
    node.removeEventListener(eventName, fn)
  },
  find(selector, scope) {
    //Â¶ÇÊûúÂ≠òÂú®scopeÁöÑËåÉÂõ¥ÔºåÂ∞±Âú®Ëøô‰∏™ËåÉÂõ¥ÈáåÈù¢ÊâæÔºõÊ≤°ÊúâÁöÑËØùÔºåÂú®documentÁöÑËåÉÂõ¥ÈáåÈù¢Êâæ
    return (scope || document).querySelectorAll(selector)
  },
  parent(node) {
    return node.parentNode
  },
  children(node) {
    return node.children
  },
  siblings(node) {
    //Êää‰º™Êï∞ÁªÑÂèòÊàêÊï∞ÁªÑÔºåËøáÊª§Âá∫ÂΩìÂâçËäÇÁÇπ
    return Array.from(node.parentNode.children).filter(item => item !== node)
  },
  next(node) {
    // ËøôÁßçÊñπÊ≥ï‰ºöËé∑ÂèñÂà∞ÊñáÊú¨ËäÇÁÇπ
    // return node.nextSibling
    let x = node.nextSibling
    while (x && x.nextSibling === 3) {
      x = x.nextSibling
    }
    return x
  },
  previous() {
    let x = node.previousSibling
    while (x && x.previousSibling === 3) {
      x = x.previousSibling
    }
    return x
  },
  each(nodeList, fn) {
    for (let i = 0; i < nodeList.length; i++) {
      fn.call(null, nodeList[i])
    }
  },
  index(node) {
    const list = dom.children(node)
    let i
    for (i = 0; i < list.length; i++) {
      if (list[i] === node) {
        break
      }
    }
    return i
  }
}